import Mock from 'mockjs'
export default {
  getMenu: config => {
    const { user_name, user_pwd } = JSON.parse(config.body);
    console.log(JSON.parse(config.body));
	if(user_name === ''){
		return{
			code: -222,
			data: {
			message: '用户名不能为空'
		 }
		}
	}
	if(user_pwd === ''){
		return{
			code: -666,
			data: {
			message: '密码不能为空'
		 }
		}
	}
    // 先判断用户是否存在
    if (user_name === 'admin' || user_name === 'xiaoxiao') {
      // 判断账号和密码是否对应
      if (user_name === 'admin' && user_pwd === '123456') {
        return {
          code: 20000,
          data: {
            menu: [
              {
                path: '/home',
                name: 'home',
                label: '首页',
                icon: 's-home',
                url: 'Home.vue'
              },{
                path: '/user',
                name: 'user',
                label: '用户管理页',
                icon: 'user',
                url: 'User.vue'
              },{
                path: '/course',
                name: 'course',
                label: '消息管理页',
                icon: 'notebook-1',
                url: 'Course.vue'
              },
              {
				  path:'/',
				  name:'other',
                label: '其他',
                icon: 'location',
                children: [
                  {
                    path: '/page1',
                    name: 'page1',
                    label: '页面1',
                    icon: 'notebook-1',
                    url: 'Page1.vue'
                  },
                  {
                    path: '/page2',
                    name: 'page2',
                    label: '页面2',
                    icon: 'notebook-1',
                    url: 'Page2.vue'
                  }
                ]
              }
            ],
			// data中获取token值
			token:Mock.Random.guid(),
            message: '获取成功',
			
          }
        }
      } else if (user_name === 'xiaoxiao' && user_pwd === '123456') {
        return {
          code: 20000,
          data: {
            menu: [
              {
                path: '/',
                name: 'home',
                label: '首页',
                icon: 's-home',
                url: 'Home.vue'
              },{
                path: '/course',
                name: 'course',
                label: '课程管理页',
                icon: 'notebook-1',
                url: 'Course.vue'
              }
            ],
			// data中获取token值
			token:Mock.Random.guid(),
            message: '获取成功'
          }
        }
      } else {
        return {
          code: -999,
          data: {
            message: '密码错误'
          }
        }
      }
    } else {
      return {
        code: -666,
        data: {
          message: '用户不存在'
        }
      }
    }
  }
}
