import Cookie from 'js-cookie'
export default {
 	state: {
 		//导航菜单状态
 		isCollapse:false,
 		// 标签组件数据
		currentMenu:null,
 		tabsList:[{
					path: '/home',
					label: '首页',
					icon: "s-home"
				}
 		],
		menu:[]
 	},
 	getters: {

 	},
 	mutations: {//修改state
	setMenu(state,val){//设置菜单
	   // val通过接口请求到的menu数据
	   state.menu=val 
	   // 向Cookie中存入menu
	   Cookie.set('menu',JSON.stringify(val))	
	},
	clearMenu(state){//退出登录时清空menu
	    state.menu=''
		Cookie.remove('menu')
		
	},
	// 添加菜单 设置动态路由
    addMenu(state,router){
	   // 从缓存中把菜单拿出来
	   if(!Cookie.get('menu'))
	   {
	    return
	   }
	   let menu = JSON.parse(Cookie.get('menu'))
	   state.menu = menu
	   // 添加动态路由 主路由CourseMain.vue
	   let currentMenu = [
	    {
	     path:'/',
	     component:() => import(`@/views/Main.vue`), //es6提供的形式,
	     children:[]
	    }
	   ]
	   // 如果menu 是一级菜单 可以获取到url 如果是二级路由 肯定没有URL 她的子路由肯定有url
	   menu.forEach(item => {
	    // 判断是否存在子路由
	    if(item.children)
	    {
	     //有子路由
	     item.children = item.children.map(item =>{
	      item.component = () => import(`@/views/${item.url}`) //${}获取变量的 es6提供的一种方式
	      return item
	     })
	     currentMenu[0].children.push(...item.children) //展开符 将children展开
	    }else{
	     // 没有子路由
	     item.component = () => import(`@/views/${item.url}`) 
	     currentMenu[0].children.push(item)
	    }
	   });
	   // console.log(currentMenu[0])
	   // 设置动态路由  addRoutes目前已废弃
	   router.addRoute(currentMenu[0])
	  },
		//改变导航菜单状态
		nomenu(state){
			state.isCollapse=!state.isCollapse
		},
	selectMenu(state,val){
				//面包屑、tag选项标签
				if(val.name === 'home'){
					state.currentMenu = null
				}else{
					state.currentMenu=val
					//如果等于 -1 那么就说明数组中不存在这个对象，那么就直接插入，否则就什么都不做
					let result = state.tabsList.findIndex(item => item.name === val.name) 
					result === -1 ? state.tabsList.push(val) : ''
				}
				Cookie.set('tabsList',JSON.stringify(state.tabsList))
				// val.name === 'home' ? (state.currentMenu=null) : (state.currentMenu=val)
			},
			closeTab(state,val){
				let result = state.tabsList.findIndex(item => item.name === val.name) 
				state.tabsList.splice(result,1)
				Cookie.set('tabsList',JSON.stringify(state.tabsList))
			},
			//获取标签
			getMenu(state){
				if(Cookie.get('tabsList')){
					let tabsList=JSON.parse(Cookie.get('tabsList'))
					state.tabsList=tabsList
				}
			},

 	},
 	actions: {

 	}
 }
