

export default {
	state: {
		//未读消息
		tableData: [{
		  date: '【系统通知】该系统将于今晚凌晨2点到5点进行升级维护',
		  name: '2022-03-02',
		}, {
		  date: '4月29日赤影疾锋上架调整说明',
		  name: '2022-04-02',
		},{
		  date: '公告4月28日英雄平衡性调整丨扁鹊、金蝉增强，夏洛特、老夫子降温',
		  name: '2022-03-02',
		},{
		  date: '公告4月26日体验服停机更新公告',
		  name: '2022-03-02',
		}],
		//已读消息
		newsdata:[
			{
			  date: '【系统通知】该系统将于今晚凌晨2点到5点进行升级维护',
			  name: '2022-05-02',
			}, {
			  date: '【系统通知】该系统将于今晚凌晨2点到5点进行升级维护',
			  name: '2022-05-02',
			},
		],
		//回收站
		recycle:[]
	},
	getters: {

	},
	mutations: {
		tabClick(state,val,index){
			//删除数组中的指定元素
			state.tableData.splice(index,1)
			state.newsdata.push(val)
		},
		newsClick(state,val,index){
			//删除数组中的指定元素
			state.newsdata.splice(index,1)
			state.recycle.push(val)
		},
		recClick(state,val,index){
			state.recycle.splice(index,1)
			state.newsdata.push(val)
		},
		// 全部已读
		tabWnt(state){
			var tableData=state.tableData
			var index=tableData.length
			tableData.forEach((item,index)=>{
				state.newsdata.push(item)
				
			})
			tableData.splice(0,index)
		},
		// 全部删除
		newWnt(state){
			var newsdata=state.newsdata
			var index=newsdata.length
			newsdata.forEach((item,index)=>{
				state.recycle.push(item)
				
			})
			newsdata.splice(0,index)
		},
		recWnt(state){
			var recycle=state.recycle
			var index=recycle.length
			recycle.forEach((item,index)=>{
				state.newsdata.push(item)	
			})
			recycle.splice(0,index)
		}
		
	},
	actions: {

	}
}

